import React from 'react'
import { useContext } from 'react';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import { AppContext } from '../context/AppContext';
import { Link } from 'react-router-dom';
import useFetch from '../useFetch';


const HeroSection = () => {
    const url = `https://api.themoviedb.org/3/trending/all/day?api_key=d96392b0ad7e22f0d27a748d2469a642`
    const{data, loading, error} = useFetch(url);
    // const { loading, trendingMovies } = useContext(AppContext)
    // console.log({trendingMovies.id});
  return (
    <div className=' hidden sm:block md:block lg:block'>
        <Carousel
            showThumbs={false}
            autoPlay={true}
            transitionTime={3}
            infiniteLoop={true}
            showStatus={false}
        >
            {
                data.map((movie) => (
                       <div key={movie.id}>
                            <div className='w-full h-[600px] relative shadow-xl'>
                                <div className='absolute top-0 ' >
                                    <img src={`https://image.tmdb.org/t/p/original/${movie && movie.backdrop_path}`} 
                                    className=' z-10  object-contain'/>
                                </div>
                                <div className=' flex-row  text-white font-semibold  z-50 absolute top-[75%] '>
                                    <h1 className='text-3xl font-extrabold text-start ml-14 '>{movie.title}</h1>
                                    <span className='flex justify-start my-2 ml-14 gap-4'>
                                        <p>{`Realse Date ${movie.release_date}`}</p>
                                        <p>{` ${movie.vote_average}`}</p>
                                    </span>
                                    <p className='w-[50%] mb-0 text-sm ml-14 text-left'>{movie.overview.length > 100 ? (movie.overview.substr(0,100)): (movie.overview)}</p>
                                </div>
                            </div>
                       </div>
                ))
            }
        </Carousel>
    </div>
  )
}

export default HeroSection