import React from 'react'
import HeroSection from '../components/HeroSection'
import Card from '../components/Card'
import { AppContext } from '../context/AppContext';
import { useContext } from 'react';

const Home = ({data}) => {
    // const { trendingMovies } = useContext(AppContext)


  return (
    <div>
        <HeroSection/>
        <div>
            <h1 className='font-bold  my-3 text-white text-[1.8rem] ml-14 uppercase'>Popular</h1>
            <div className='grid  grid-cols-3 md:grid-cols-6 h-[1100px] gap-1  w-10/12 mx-auto '>
                
                {
                    data.map( (movie,id) => (
                        <div>
                            <Card key={id} movie={movie}  />
                        </div>

                    ))
                }
            </div>
        </div>
    </div>
  )
}

export default Home